.view-type label{
    margin-left:10px;
}
.field {
    /* border-radius: 4px; */
    padding: 8px 10px;
    margin: 5px;
    background: #f5f5f5;
    color: #4B5966;
    font-size: 14px;
    letter-spacing: 0.6px;
    overflow: hidden;
    text-overflow: ellipsis;
	box-sizing: border-box;
	border-radius: 2px;
    border: 1px solid #ccc;
}


.field.activeField{
	background-color: rgba(66, 66, 66, .5) ;
}

.field span{
	float:right;
	margin:0px 5px;
}
.col-cont{
	height:100%;
}

.column .title {
	width: 100%;
	padding: 0 10px;
	text-transform: capitalize;
	display: inline-block;
}

.column .activeColumn {
	background-color: #dcf7e0;
	-webkit-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;

}

.view-empty-placeholder{
	min-height:28px;
	height: 100%;
	margin:5px;
	/* border-radius: 4px; */
	text-align: center;
	border: 1px solid #fff;
	color: #8294A5;
	box-sizing: border-box;
	cursor: pointer;
}
.view-empty-placeholder:hover{
	border: 1px dashed #e2e4e7;
}
.view-empty-placeholder i{
	margin: 6px 0px;
}

.droppableColumn{
	min-height: 41px;
}
.row {
	/* border: 1px solid red; */
	position: relative;
	padding: 10px;
    margin: 15px 0px;
    background: #fff;
	/* box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.15);
	border-radius:4px; */
	border: 1px solid #e2e4e7;
}

.view-setting-icon{
	display: inline-block;
	width:25px;
}
.rowSettingsPanel{
	background:#f5f5f5;
    padding: 5px 10px;
	margin: -10px 0 5px -10px;
	font-size: 16px;
	color: #4B5966;
	border-top-left-radius: 4px;
}

.columnPanel{
	/* padding:10px 10px 10px 60px; */
	width:100%;
	box-sizing: border-box;
}
.addRow{
	padding:5px;
	cursor: pointer;
	box-sizing: border-box;
	font-size:16px;
}

.addRow span{
	color:#8294A5;

}

.viewSectionContainer {
	padding: 5px 15px 10px;
	/* box-shadow: 0px 0px 7px 1px #EBEBEB; */
	margin-bottom: 25px;
	/* border-radius: 4px; */
	/* border:1px solid #e5e5e5 */
	border:1px solid #e5e5e5;
	background: #fff;
}

#views-container h2.viewSecHeading {
	font-size: 18px;
	color: #4B5966;
	text-transform: capitalize;
	font-weight: 700;
	letter-spacing: .3px;
}
.viewSecMainCont{
	background: #fff;
}
.viewTabChild {
	margin-left: 10px;
	margin-right: 10px;
	margin-top: 10px;
	background: #f7f7f7;
	border: 1px solid #ccc;
}
.viewTabChild:last-child{
	margin-bottom:10px;
}
.viewTabHeadings li {
	line-height: 1.6;
}

.viewTabHeadings li:first-child,
.viewTabHeadings li:first-child a {
	border-top-left-radius: 4px;
}
.viewTabHeadings li a{
	color: #4B5966;
}
.pure-menu-active>.pure-menu-link,
.pure-menu-link:focus,
.pure-menu-link:hover,
.viewTabActive {
	background-color:#f5f5f5;
}

.viewSubHead {
	font-size: 16px;
	color: #6f767b;
	font-weight: 600;
}

.viewInlineSec label {
	margin-right: 10px;
	font-size: 16px;
	width: 175px;
	display: inline-block;
}

.viewInlineSec {
	margin-bottom: 10px;
}
.viewHide{
	display:none !important;
}


.add-fields-list{
	list-style: none;
	width: 100%;
    box-sizing: border-box;
    margin: 0px;
    padding: 20px;
}

.add-fields-list li{
	padding:10px;
	width: 400px;
	margin:20px 0px;
	cursor: pointer;
	/* border-radius: 4px;
	box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.15); */
	border: 1px solid #e5e5e5;
}
.add-fields-list li:first-child{
	margin-top:0px;
}
.add-fields-list li:last-child{
	margin-bottom:0px;
}
.fieldSettings{
	padding:20px;
}
.fieldSettings .pure-button{
	margin-right:20px;
}
.fieldSettings label{
	text-align: left;
}
.fieldSettings .form-action-btns{
	margin-top: 30px;
}
.modal-container {
    max-width: 800px;
    background: #fff;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: auto;
    border: 1px solid #e2e4e7;
    /* border-radius: 4px;
	box-shadow: 0px 0px 25px 0px rgba(0,0,0,.3); */
}

.modal-container:focus {
    outline: 0px;
}

.layoutCol:first-child .layoutColStyle {
    margin-left: 0px;
}

.layoutCol:last-child .layoutColStyle {
    margin-right: 0px;
}

.container {
    list-style-type: none;
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    margin: 0px;
    padding: 10px;
}

.layoutHead {
    padding: 10px 21px;
    background-color: #f3f4f5;
    color: #4B5966;
    position: -webkit-sticky;
    position: sticky;
    top: -20px;
    z-index: 9;
}

.layoutHeading {
    font-size: 20px;
    margin: 0px;
    font-weight: 400;
}

.container li {
    float: left;
    width: 32.3%;
    margin: 0px 1.5% 20px 0;
    padding: 10px 10px;
    cursor: pointer;
    box-sizing: border-box;
    /* border-radius: 4px; */
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.05);
}

.container li:nth-child(3n) {
    margin-right: 0px;
}

.closeLayoutOption {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    padding: 10px 21px;
    background-color: #f3f4f5;
    font-size: 24px;
    cursor: pointer;
}

.layoutCol {
    /* float: left; */
    position: relative;
}

.layoutColStyle {
    margin: 0px 5px;
    height: 40px;
    background-color: #EBEBEB;
    color: #4B5966;
    text-align: center;
    /* border-radius: 4px; */
}

.layoutName {
    padding-top: 12px;
    display: inline-block;
    font-size: 16px;
}

.modal-portal {
    background: #fff;
}
.modal-portal.addField .modal-container, .modal-portal.fieldSettings .modal-container{
	max-width:450px;
}

/* last row element has no margin */

.container li.view-layout-last-row {
    margin-bottom: 0px;
}
.viewSourceCont{
	margin-top: 20px;
}
.viewSecMainCont{
	border: 1px solid #e5e5e5;
	/* border-radius: 4px; */
	margin-bottom:25px;
}
.view-child{
	margin-left:10px;
}
.pure-form{
	font-size:14px;
}
.pure-form select{
	padding-top:0px;
	padding-bottom:0px;
}
.final-view-settings{
	display: none;
}
.pro-features{
	background: #ccc;
	padding: 2px 2px;
	border-radius:4px;
}
